import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Box,
  Typography,
  CircularProgress,
  Alert,
} from '@mui/material';
import { candidatesAPI, clientsAPI, CandidateDTO, ClientDTO } from '../services/api';

interface GenerateInvoiceModalProps {
  open: boolean;
  onClose: () => void;
  onGenerate: (data: GenerateInvoiceData) => void;
}

interface GenerateInvoiceData {
  candidateId: string | 'all';
  clientId: string | 'all';
  week: string;
  invoiceDate: string;
}

const GenerateInvoiceModal: React.FC<GenerateInvoiceModalProps> = ({
  open,
  onClose,
  onGenerate,
}) => {
  const [candidates, setCandidates] = useState<CandidateDTO[]>([]);
  const [clients, setClients] = useState<ClientDTO[]>([]);
  const [selectedCandidate, setSelectedCandidate] = useState<string>('');
  const [selectedClient, setSelectedClient] = useState<string>('');
  const [selectedWeek, setSelectedWeek] = useState<string>('');
  const [invoiceDate, setInvoiceDate] = useState<string>('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Generate week options (Monday to Sunday, like CreateTimesheetModal)
  const generateWeekOptions = () => {
    const weeks = [];
    const today = new Date();
    
    // Generate weeks for the past 2 years and future 1 year
    for (let i = -104; i <= 52; i++) { // 2 years back, 1 year forward
      const weekDate = new Date(today);
      weekDate.setDate(today.getDate() + (i * 7));
      
      // Find the Monday of this week
      const weekStart = new Date(weekDate);
      const dayOfWeek = weekDate.getDay();
      const daysToMonday = dayOfWeek === 0 ? 6 : dayOfWeek - 1; // Sunday = 0, so 6 days to Monday
      weekStart.setDate(weekDate.getDate() - daysToMonday);
      
      const weekEnd = new Date(weekStart);
      weekEnd.setDate(weekStart.getDate() + 6); // End of week (Sunday)
      
      const startDate = weekStart.toLocaleDateString('en-GB', { 
        day: 'numeric', 
        month: 'short' 
      });
      const endDate = weekEnd.toLocaleDateString('en-GB', { 
        day: 'numeric', 
        month: 'short',
        year: 'numeric'
      });
      
      const weekLabel = `Week of ${startDate} - ${endDate}`;
      
      weeks.push({
        value: weekStart.toISOString().split('T')[0],
        label: weekLabel
      });
    }
    
    return weeks;
  };

  const weekOptions = generateWeekOptions();

  useEffect(() => {
    if (open) {
      fetchData();
    }
  }, [open]);

  const fetchData = async () => {
    try {
      setLoading(true);
      setError(null);
      
      // Fetch active candidates and clients in parallel
      const [candidatesResponse, clientsResponse] = await Promise.all([
        candidatesAPI.listActive(),
        clientsAPI.list()
      ]);
      
      setCandidates(candidatesResponse);
      setClients(clientsResponse.items || []);
    } catch (err) {
      console.error('Error fetching data:', err);
      setError('Failed to load candidates and clients');
    } finally {
      setLoading(false);
    }
  };

  const handleGenerate = () => {
    if (!selectedCandidate || !selectedClient || !selectedWeek || !invoiceDate) {
      setError('Please fill in all fields');
      return;
    }

    const generateData: GenerateInvoiceData = {
      candidateId: selectedCandidate,
      clientId: selectedClient,
      week: selectedWeek,
      invoiceDate: invoiceDate,
    };

    onGenerate(generateData);
  };

  const handleClose = () => {
    setSelectedCandidate('');
    setSelectedClient('');
    setSelectedWeek('');
    setInvoiceDate('');
    setError(null);
    onClose();
  };

  return (
    <Dialog open={open} onClose={handleClose} maxWidth="sm" fullWidth>
      <DialogTitle>
        <Typography variant="h6" component="div">
          Generate Invoice
        </Typography>
      </DialogTitle>
      
      <DialogContent>
        <Box sx={{ pt: 2 }}>
          {loading ? (
            <Box display="flex" justifyContent="center" p={3}>
              <CircularProgress />
            </Box>
          ) : (
            <>
              {error && (
                <Alert severity="error" sx={{ mb: 2 }}>
                  {error}
                </Alert>
              )}
              
              <FormControl fullWidth sx={{ mb: 3 }}>
                <InputLabel>Select Candidate</InputLabel>
                <Select
                  value={selectedCandidate}
                  onChange={(e) => setSelectedCandidate(e.target.value)}
                  label="Select Candidate"
                >
                  <MenuItem value="all">
                    <strong>All Candidates</strong>
                  </MenuItem>
                  {candidates.map((candidate) => (
                    <MenuItem key={candidate.candidate_id} value={candidate.candidate_id}>
                      {candidate.invoice_contact_name || 'Unknown Candidate'}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <FormControl fullWidth sx={{ mb: 3 }}>
                <InputLabel>Select Client</InputLabel>
                <Select
                  value={selectedClient}
                  onChange={(e) => setSelectedClient(e.target.value)}
                  label="Select Client"
                >
                  <MenuItem value="all">
                    <strong>All Clients</strong>
                  </MenuItem>
                  {clients.map((client) => (
                    <MenuItem key={client.client_id} value={client.client_id}>
                      {client.client_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <FormControl fullWidth sx={{ mb: 3 }}>
                <InputLabel>Select Week</InputLabel>
                <Select
                  value={selectedWeek}
                  onChange={(e) => setSelectedWeek(e.target.value)}
                  label="Select Week"
                >
                  {weekOptions.map((week) => (
                    <MenuItem key={week.value} value={week.value}>
                      {week.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <TextField
                fullWidth
                label="Invoice Date"
                type="date"
                value={invoiceDate}
                onChange={(e) => setInvoiceDate(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
                sx={{ mb: 3 }}
              />
            </>
          )}
        </Box>
      </DialogContent>
      
      <DialogActions>
        <Button onClick={handleClose} color="secondary">
          Cancel
        </Button>
        <Button 
          onClick={handleGenerate} 
          variant="contained"
          disabled={loading || !selectedCandidate || !selectedClient || !selectedWeek || !invoiceDate}
        >
          Generate Invoice
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default GenerateInvoiceModal;
